@tailwind base;
@tailwind components;
@tailwind utilities;


.font-pixel{
    font-family: 'Press Start 2P', cursive;
}

.videoB{
width: 100%;
height: 200vh;
  object-fit: cover;
  z-index: -1;
  }

  .content {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    color: #fff;
  }

  .paddingcontent{
    padding-left: 20%;
    padding-right: 20%;
  }

  .paddingcol{
    padding-left: 12%;
    padding-right: 12%;
  }

  .myvideo {
    width: 100%;
    height: 100%;
    z-index: -1;
  }

  .scroll{
    max-height: 55rem;
    overflow-y: scroll;
    overflow-x: hiden;
    scrollbar-color: rgba(20, 20, 20, 0.3)  black;
  }

  .scrollx{
    scrollbar-color: rgba(20, 20, 20, 0.3)  black;
    overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  
  }


  .moveX{
    display: inline-flex;
    padding-left: 2rem;
  }


  .blackwindow
  {
    background-color: rgba(37, 36, 36, 0.5);
    -webkit-border-radius: 7px;
     border-radius: 7px;
  }
  
  .center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    border-radius: 10px; 
    overflow: hidden; 
  }

  .imgred{
    
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    border-radius: 20px; 
    overflow: hidden; 
  }

  .neonborder4{
    border: 5px #5608c8 solid;
    box-shadow: 1px 1px 20px 10px #985cf3;
  }

  .neonborder2{
    border: 5px #63dd17 solid;
    box-shadow: 1px 1px 20px 10px #68ecb9;
  }

  .neonborder1{
    border: 5px #ff002e solid;
    box-shadow: 1px 1px 20px 10px #f15c68;
  }

  .neonborder3{
    border: 5px #ffa4d5 solid;
    box-shadow: 1px 1px 20px 10px #f0b9dd;
  }

  .neonborder5{
